import { Component, OnInit } from '@angular/core';
import { PmService } from '../pm.service';
import { Post } from '../Post';

@Component({
  selector: 'app-restaurant',
  templateUrl: './restaurant.component.html',
  styleUrls: ['./restaurant.component.css']
})
export class RestaurantComponent implements OnInit {
  private posts: Post[] = [];
  private errorMessage: any = '';
  private wait = Math.floor((Math.random()*60)+1);
    constructor(private postService: PmService) {
      setInterval(
        function(){
        this.wait = Math.floor((Math.random()*60)+1); }, 300);

    }
    //setInterval(function(){this.wait = Math.floor((Math.random()*60)+1); }, 300);
      ngOnInit() {
        this.getPosts();
        //this.wait = wait();
        setInterval(
          function(){
          this.wait = Math.floor((Math.random()*60)+1); }, 300);
    }
      onSubmit(num){
        console.log(num);
      }

      getPosts() {
        this.postService.getData()
          .subscribe(
              posts => this.posts = posts,
              //wait => this.wait = wait(),
              error => this.errorMessage = <any>error);
      }

}



